body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background: #fff;
  }
  .box {
    width: 300px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    text-align: center;
    border-radius: 18px;
    border: solid #5f7a6e 2px;
  
  }
  .box h1 {
      color:#5f7a6e;
      text-transform: uppercase;
      font-weight: 500;
  }
  .box input[type="text"],
  .box input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #5f7a6e;
    padding: 14px 10px;
    width: 200px;
    outline: none;
    color: black;
    border-radius: 24px;
    transition: 0.25s;
  }
  .box input[type="text"]:focus,
  .box input[type="password"]:focus {
      width: 280px;
      border-color: #2ecc71;
  }
  .box input[type = "submit"]{
      border: 0;
      background: none;
      display: block;
      margin: 20px auto;
      text-align: center;
      border: 2px solid #2ecc71;
      padding: 14px 40px;
      width: 200px;
      outline: none;
      color:#5f7a6e;
      border-radius: 24px;
      transition: 0.25s;
      cursor: pointer;
  }
  .box input[type = "submit"]:hover{
      background: #2ecc71;
      color: white;
  }
  