body {
    font-family: Arial, Helvetica, sans-serif;
  }
  .wrapform {
    max-width: 350px;
    border-radius: 20px;
    margin: auto;
    background: #5f7a6e;
    box-sizing: border-box;
    padding: 40px;
    color: #fff;
    margin-top: 100px;
  }
  h2 {
    text-align: center;
  }
  input[type="text"],
  input[type="password"],
  textarea {
    width: 100%;
    box-sizing: border-box;
    padding: 12px 5px;
    background: white;
    outline: none;
  
    border-bottom: 1px dotted #fff;
    border-radius: 5px;
    margin: 5px;
  }
  input[type="submit"] {
    width: 100%;
    box-sizing: border-box;
    padding: 10px 0;
    margin-top: 30px;
    outline: none;
    background: white;
    border: solid black;
    border-radius: 20px;
    font-size: 20px;
    color: #5f7a6e;
  }
  input[type="submit"]:hover {
    background: #2ecc71;
  
    color: white;
  }
  