body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #fff;
}
.logo{
  width: 50px;
  height: 50px;
 
}
.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  background: white;
  border: 2px solid #5f7a6e;
  border-radius: 18px;
}
.navbarlinks{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  
}
.btnavbar {
  text-decoration: none;
  border-color: black;
  margin: 10px;
  color:#5f7a6e;
}
@media (min-width: 200px) {
  .btnavbar {
    margin: 5px;
  }
}



